"use client"

import { Button } from "@/components/ui/button"
import { Card } from "@/components/ui/card"
import { Plus, Edit2, Trash2, Eye } from "lucide-react"
import Link from "next/link"
import { useEffect, useState } from "react"
import { vehiclesApi, type Vehicle } from "@/lib/api/vehicles"

export default function VehiclesPage() {
  const [vehicles, setVehicles] = useState<Vehicle[]>([])
  const [isLoading, setIsLoading] = useState(true)

  useEffect(() => {
    loadVehicles()
  }, [])

  const loadVehicles = async () => {
    try {
      const data = await vehiclesApi.getAll()
      setVehicles(data)
    } catch (error) {
      console.error("Failed to load vehicles:", error)
    } finally {
      setIsLoading(false)
    }
  }

  const handleDelete = async (id: string) => {
    if (!confirm("Are you sure you want to delete this vehicle?")) return
    
    try {
      await vehiclesApi.delete(id)
      setVehicles(vehicles.filter((v) => v.id !== id))
    } catch (error) {
      console.error("Failed to delete vehicle:", error)
      alert("Failed to delete vehicle")
    }
  }

  return (
    <div className="p-8 bg-muted/20 min-h-screen">
      <div className="max-w-6xl mx-auto space-y-8">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-foreground">Vehicles</h1>
            <p className="text-muted-foreground mt-2">Manage all your vehicles in one place</p>
          </div>
          <Button asChild>
            <Link href="/app/vehicles/new" className="gap-2">
              <Plus size={18} />
              Add Vehicle
            </Link>
          </Button>
        </div>

        {/* Vehicles Table */}
        {vehicles.length === 0 ? (
          <Card className="p-12 text-center">
            <h2 className="text-2xl font-bold text-foreground mb-3">No vehicles yet</h2>
            <p className="text-muted-foreground mb-6">Add your first vehicle to get started</p>
            <Button asChild>
              <Link href="/app/vehicles/new" className="gap-2">
                <Plus size={18} />
                Add Vehicle
              </Link>
            </Button>
          </Card>
        ) : (
          <Card className="overflow-hidden">
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead className="border-b bg-muted/50">
                  <tr>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">License Plate</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Vehicle</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Year</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Status</th>
                    <th className="text-left px-6 py-4 text-sm font-semibold text-foreground">Last Service</th>
                    <th className="text-right px-6 py-4 text-sm font-semibold text-foreground">Actions</th>
                  </tr>
                </thead>
                <tbody className="divide-y divide-gray-200">
                  {vehicles.map((vehicle) => (
                    <tr key={vehicle.id} className="hover:bg-muted/50">
                      <td className="px-6 py-4 text-sm font-medium text-foreground">{vehicle.plate}</td>
                      <td className="px-6 py-4 text-sm text-muted-foreground">
                        {vehicle.brand} {vehicle.model}
                      </td>
                      <td className="px-6 py-4 text-sm text-muted-foreground">{vehicle.year}</td>
                      <td className="px-6 py-4 text-sm">
                        <span
                          className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${
                            vehicle.status === "owned" ? "bg-green-100 text-green-800" : "bg-gray-100 text-gray-800"
                          }`}
                        >
                          {vehicle.status === "owned" ? "Owned" : "Sold"}
                        </span>
                      </td>
                      <td className="px-6 py-4 text-sm text-muted-foreground">{vehicle.lastService || "â€”"}</td>
                      <td className="px-6 py-4 text-right">
                        <div className="flex items-center justify-end gap-2">
                          <Link
                            href={`/app/vehicles/${vehicle.id}`}
                            className="p-1.5 text-muted-foreground hover:bg-muted rounded-lg transition"
                            title="View"
                          >
                            <Eye size={18} />
                          </Link>
                          <Link
                            href={`/app/vehicles/${vehicle.id}/edit`}
                            className="p-1.5 text-muted-foreground hover:bg-muted rounded-lg transition"
                            title="Edit"
                          >
                            <Edit2 size={18} />
                          </Link>
                          <button
                            onClick={() => handleDelete(vehicle.id)}
                            className="p-1.5 text-muted-foreground hover:bg-destructive/10 hover:text-destructive rounded-lg transition"
                            title="Delete"
                          >
                            <Trash2 size={18} />
                          </button>
                        </div>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </Card>
        )}
      </div>
    </div>
  )
}
